import { useState } from "react";
import "./styles.css";

export default function App() {
  const [data, setData] = useState({
    maxPages: 6,
    page: 1
  });

  function updateData(type, newData) {
    setData((data) => {
      return { ...data, [type]: newData };
    });
  }

  function nextPage() {
    if (data.page === data.maxPages) {
      return null;
    }
    updateData("page", data.page + 1);
  }

  function lastPage() {
    if (data.page === 1) {
      return null;
    }
    updateData("page", data.page - 1);
  }

  return (
    <div className="App">
      {/* progress bar */}
      <div>
        Page {data.page} of {data.maxPages}
      </div>
      <button onClick={lastPage}>Last</button>
      <span> </span>
      <button onClick={nextPage}>Next</button>
      <hr />

      {/* forms */}
      <div>
        <Onboarding pageNum={data.page} />
      </div>
      <hr />
      <p>EZ APPLY</p>
    </div>
  );
}

function Onboarding(props) {
  if (props.pageNum === 1) {
    return (
      <div>
        <p>First Name</p>
        <p>Last Name</p>
        <p>Last 4 Of SSN</p>
      </div>
    );
  } else if (props.pageNum === 2) {
    return (
      <div>
        <p>Work History</p>
        <p>CDL</p>
        <p>Citations</p>
      </div>
    );
  } else if (props.pageNum === 3) {
    return (
      <div>
        <p>Signed Document 1 of 3</p>
        <p>(Place your vendor iframe here)</p>
      </div>
    );
  } else if (props.pageNum === 4) {
    return (
      <div>
        <p>Signed Document 2 of 3</p>
        <p>(Place your vendor iframe here)</p>
      </div>
    );
  } else if (props.pageNum === 5) {
    return (
      <div>
        <p>Signed Document 3 of 3</p>
        <p>(Place your vendor iframe here)</p>
      </div>
    );
  } else {
    return (
      <div>
        <p>You're application is being reviewed. Thank You!</p>
      </div>
    );
  }
}
